import React, { Fragment } from 'react';
import { Link } from 'gatsby';
import { routes } from '../constants';

import SEO from '../components/SEO';

const NotFoundPage = () => {
  return (
    <Fragment>
      <SEO
        title="404"
        description="Oops! The page you are looking for does not exist."
      />
      <div className="max-height flex items-center justify-center">
        <div className="contain-sm text-center mt-8">
          <h1 className="text-3xl md:text-4xl md:text-5xl">404</h1>
          <p className="text-mdgray my-8 md:text-lg">
            Oops! The page you are looking for does not exist.
          </p>
          <Link to={routes.HOME} className="link md:text-lg">
            Back To Home
          </Link>
        </div>
      </div>
    </Fragment>
  );
};

export default NotFoundPage;
